















































































































































































































































































































































import Vue from "vue";
import Component from "vue-class-component";
import axios from "axios";
import * as indexApi from "../../api/indexApi";
import moment from "moment";
import { Prop } from "vue-property-decorator";
import prescriptioninfo from "../transmit/prescriptioninfo";
import updateorder from "./updateorder";

@Component({
  props: {},
  components: {
    prescriptioninfo,
    updateorder
  }
})
export default class AddGoods extends Vue {
  @Prop({ required: false })
  orderList: any;
  @Prop({ required: false })
  pagetype: string;
  @Prop({ required: false })
  patModel: string;
  @Prop({ required: false })
  provinceList;




handleInvoinStatus(row){
if(row.invoiceRecords && row.invoiceRecords.length>0&& row.invoiceRecords[0].shipStatus == 'HAVE_SIGNED_IN'){
  return {title:"开票完成",type:'success'}
}else if(row.isInvoicedShipped == '1'){
  return {title:"已开发票",type:'warning'}
}else if(row.isInvoiced == '1'){
  return {title:"新增开票",type:'danger'}
}else if(row.isInvoiced == '0'){
  return {title:"未开发票",type:'info'}
}else{
  return {title:"",type:''}
}
}




  invoiceRecordsObj = {};
  invoiceRecordModel = false;
  getInvoiceRecords(index, row) {
    this.invoiceRecordsObj = row.invoiceRecords;
  }
  giveupModel = false;
  presId = "";
  reason = "";
  doGiveup(row) {
    this.presId = row.presId;
    this.reason = "";
    this.giveupModel = !this.giveupModel;
  }
  doGiveupSubmit() {
    this.$confirm("是否对该订单弃单?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        if ((this.reason || "") == "") {
          this.$message("请输入备注原因");
          return;
        }
        indexApi
          .doGiveup({
            preId: this.presId,
            reason: this.reason
          })
          .then(res => {
            if (res["retCode"]) {
              this.$message("已弃单");
              this.$emit("getOrderList");
              this.giveupModel = !this.giveupModel;
            } else {
              if (!res["islogin"]) {
                this.$alert(res["message"]);
              }
              console.error("数据查询错误");
            }
          });
      })
      .catch(() => {
        this.$message({
          type: "info",
          message: "已取消操作"
        });
      });
  }
  order = {};
  doReminder(row) {
    // this.$confirm("确定要发送一次催单?", "提示", {
    //   confirmButtonText: "确定",
    //   cancelButtonText: "取消",
    //   type: "warning"
    // })
    //   .then(() => {
    indexApi
      .changeReminderStatus({
        presId: row.presId
      })
      .then(res => {
        if (res["retCode"]) {
          this.$message("已催单");
          this.$emit("getOrderList");
        } else {
          if (!res["islogin"]) {
            this.$alert(res["message"]);
          }
          console.error("数据查询错误");
        }
      });
    // })
    // .catch(() => {
    //   this.$message({
    //     type: "info",
    //     message: "已取消操作"
    //   });
    // });
  }

  getorderInfo(row) {
    //  let order = {}
    //   Object.assign(order,row);
    //   this.order = order
    let a: any = this.$refs.updateorder;
    a.model = true;
    //  a.queryPresDrugback(row.presId)

    this.getOrderDetail(row.presId);
  }
  handlePaymentMode(status) {
    switch (status) {
      case "ONLINE_PAYMENT":
        return "微信支付";
      case "ORDER_PAY_ONDEV":
        return "货到付款";
      default:
        return "";
    }
  }
  getOrderDetail(presId, callback = null) {
    indexApi
      .getOrderInfo({
        presId: presId
      })
      .then(res => {
        if (res["retCode"]) {
          res.data.YdkOrderDetailList.forEach((item, index) => {
            item.logisticslabel = item.logistics;
            item.waybillNumberlabel = item.waybillNumber;
          });
          if (res.data.invoiceRecords && res.data.invoiceRecords.length > 0) {
            res.data.invoiceObj_logistics =
              res.data.invoiceRecords[0].logistics;
            res.data.invoiceObj_waybillNumber =
              res.data.invoiceRecords[0].waybillNumber;
          }

          this.order = res.data;
          this.updateOrder = res.data;

          if (callback) {
            callback(res.data);
          }
        } else {
          if (!res["islogin"]) {
            this.$alert(res["message"]);
          }
        }
      });
  }
  updateOrder = {};
  updateInfo(row) {
    let a: any = this.$refs.updateorder;
    this.getOrderDetail(row.presId, res => {
      (<any>this.$refs.updateorder).updateOrder = res;
      (<any>this.$refs.updateorder).queryCityList();
      (<any>this.$refs.updateorder).queryCountryList();
    });
    a.formModel = true;
  }

  getOrderList() {
    this.$emit("getOrderList");
  }
  handlePayStatus(status) {
    switch (status) {
      case "PAY_WAIT":
        return "等待支付";
      case "PAY_SUCCESS":
        return "支付成功";
      default:
        return "";
    }
  }

  prescriptioninfoObj = {};
  preImageList = [];
  getTransmitInfo(index, row) {
    this.prescriptioninfoObj = row;
    let a: any = this.$refs.prescriptioninfo;
    a.getInfo();
  }
  handleOrderStatus(status) {
    switch (status) {
      case "ORDER_INIT":
        return "订单初始化";
      case "ORDER_WAIT_PAY":
        return "等待支付";
      case "ORDER_PAY_ONDEV":
        return "货到付款";
      case "ORDER_CANCEL_PAY":
        return "取消支付";
      case "ORDER_WAIT_SENDGOODS":
        return "待发货";
      case "ORDER_WAIT_RECVGOODS":
        return "待收货";
      case "ORDER_END_GOODS":
        return "完成";
      default:
        return "";
    }
  }
  shipList = [];
  queryShipList() {
    indexApi.queryShipList({}).then(res => {
      if (res["retCode"]) {
        this.shipList = res.data.KuaibirdComTrans;
      } else {
        if (!res["islogin"]) {
          this.$alert(res["message"]);
        }
        console.error("数据查询错误");
      }
    });
  }

  mounted() {
    this.queryShipList();
  }
}



