
































































































































































































































































































































































































































































































































































































































































































































































































import Vue from "vue";
import Component from "vue-class-component";
import axios from "axios";
import * as indexApi from "../../api/indexApi";
import moment from "moment";
import { Prop } from "vue-property-decorator";
import * as ApiOrder from "../../api/orderapi";

@Component({
  props: {},
  components: {}
})

/**
 * order :  订单
 *
 *
 */
export default class AddGoods extends Vue {
  @Prop({ required: false })
  order: any;
  @Prop({ required: false })
  updateOrder: any;
  @Prop({ required: false })
  pagetype: string;
  @Prop({ required: false })
  shipList;

  @Prop({ required: false })
  provinceList;

  loading = false;
  /**
   *
   * 订单状态
   */

  handleShipStatus(shipStatus) {
    switch (shipStatus) {
      case "ALREAD_SHIPPED":
        return "已发货";
      case "UNSHIPPED_DELIVERY":
        return "未发货";
      case "HAVE_SIGNED_IN":
        return "已签收";
      default:
        return "";
    }
  }
  /**
   *开发票 发货
   */
  do_invoiceObjSend(presId) {
    this.$confirm("确认保存该发货信息?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        indexApi
          .invoicedDeliver({
            logistics: this.order.invoiceObj_logistics,
            waybillNumber: this.order.invoiceObj_waybillNumber,
            preid: presId
          })
          .then(res => {
            if (res["retCode"]) {
              this.$message("发货成功");
              this.$emit("getOrderList");
              this.$emit("getOrderDetail", presId);
              this.$emit("queryShipList");
            } else {
              if (!res["islogin"]) {
                this.$alert(res["message"]);
              }
            }
          });
      })
      .catch(() => {
        this.$message({
          type: "info",
          message: "已取消操作"
        });
      });
  }
  /**
   * 发票 确认收货
   */
  recv_invoice(order) {
    this.$confirm("确认收货?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        indexApi
          .invoicedSignInByid({
            invoiceid: order.invoiceRecords[0].invoiceRecordId
          })
          .then(res => {
            if (res["retCode"]) {
              this.$message("已确认收货");
              this.$emit("getOrderList");
              this.$emit("getOrderDetail", this.order.presId);
              this.$emit("queryShipList");
            } else {
              if (!res["islogin"]) {
                this.$alert(res["message"]);
              }
            }
          });
      })
      .catch(() => {
        this.$message({
          type: "info",
          message: "已取消操作"
        });
      });
  }

  countryList = [];
  cityList = [];

  handleUpdateOrder(updateOrder) {
    this.updateOrder = updateOrder;
  }
  queryCountryList(setNull = null) {
    if (setNull) {
      this.updateOrder.areaid = "";
    }
    ApiOrder.queryCountryList(this.updateOrder.cityid).then(res => {
      this.countryList = res.data.region;
    });
  }

  queryCityList(setNull = null) {
    if (setNull) {
      this.updateOrder.cityid = "";
    }
    ApiOrder.queryCityList(this.updateOrder.provinceid).then(res => {
      this.cityList = res.data.region;
    });
  }

  shipInfoLoading = false;
  shipInfoModel = false;
  shipInfo = [];
  showShipInfo(item) {
    this.shipInfo = [];
    this.shipInfoModel = true;
    this.shipInfoLoading = true;

    indexApi
      .showShipInfo({
        id: item.waybillNumber,
        comName: item.logistics
      })
      .then(res => {
        this.shipInfoLoading = false;
        if (res["retCode"]) {
          if (res.data.Success) {
            this.shipInfo = res.data.Traces;
          }
        } else {
          if (!res["islogin"]) {
            this.$alert(res["message"]);
          }
          console.error("数据查询错误");
        }
      });
  }
  handlePaymentMode(status) {
    switch (status) {
      case "ONLINE_PAYMENT":
        return "微信支付";
      case "ORDER_PAY_ONDEV":
        return "货到付款";
      default:
        return "";
    }
  }

  recvGood(item) {
    this.$confirm("确认收货?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        indexApi
          .doSend({
            //  pres_id:this.order.presId,
            order_detail_id: item.orderDetailId,
            ship_status: "ORDER_END_GOODS"
          })
          .then(res => {
            if (res["retCode"]) {
              this.$message("已确认收货");
              this.$emit("getOrderList");
              this.$emit("getOrderDetail", this.order.presId);
              this.$emit("queryShipList");
            } else {
              if (!res["islogin"]) {
                this.$alert(res["message"]);
              }
              console.error("数据查询错误");
            }
          });
      })
      .catch(() => {
        this.$message({
          type: "info",
          message: "已取消操作"
        });
      });
  }
  handlePayStatus(status) {
    switch (status) {
      case "PAY_WAIT":
        return "等待支付";
      case "PAY_SUCCESS":
        return "支付成功";
      default:
        return "";
    }
  }
  sendModel = false;
  doSend(item, presId) {
    item.waybill_number = item.waybillNumber;
    //验证
    if ((item.logistics || "") == "") {
      this.$alert("请填写物流公司");
      return;
    }
    if ((item.waybill_number || "") == "") {
      this.$alert("请填写物流单号");
      return;
    }
    this.$confirm("确认保存该发货信息?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        indexApi
          .doSend({
            order_detail_id: item.orderDetailId,
            pres_id: presId,
            ship_status: "ORDER_WAIT_RECVGOODS",
            logistics: item.logistics,
            waybill_number: item.waybill_number
          })
          .then(res => {
            if (res["retCode"]) {
              this.$message("发货成功");
              this.$emit("getOrderList");
              this.$emit("getOrderDetail", presId);
              this.$emit("queryShipList");
            } else {
              if (!res["islogin"]) {
                this.$alert(res["message"]);
              }
            }
          });
      })
      .catch(() => {
        this.$message({
          type: "info",
          message: "已取消操作"
        });
      });
  }

  preDrugList = [];
  queryPresDrugback(presId) {
    indexApi
      .queryPresDrugback({
        preId: presId
      })
      .then(res => {
        if (res["retCode"]) {
          this.preDrugList = res.data;
        } else {
          if (!res["islogin"]) {
            this.$alert(res["message"]);
          }
          console.error("数据查询错误");
        }
      });
  }

  model = false;
  formModel = false;
  doSubmit() {
    //验证
    indexApi.updateOrder(this.updateOrder).then(res => {
      if (res["retCode"]) {
        this.$message("保存成功");
        this.formModel = false;
        this.$emit("getOrderList");
      } else {
        if (!res["islogin"]) {
          this.$alert(res["message"]);
        }
        console.error("数据查询错误");
      }
    });
  }

  handleOrderStatus(status) {
    switch (status) {
      case "ORDER_INIT":
        return "订单初始化";
      case "ORDER_WAIT_PAY":
        return "等待支付";
      case "ORDER_PAY_ONDEV":
        return "货到付款";
      case "ORDER_CANCEL_PAY":
        return "取消支付";
      case "ORDER_WAIT_SENDGOODS":
        return "待发货";
      case "ORDER_WAIT_RECVGOODS":
        return "待收货";
      case "ORDER_END_GOODS":
        return "完成";
      default:
        return "";
    }
  }

  mounted() {}
}



